/* 2-col list */

.t4.page-technology-procurement {
    .box3.general {
        padding-left: 10%;
        padding-right: 10%;
        ul {
            display: flex;
            flex-wrap: wrap;
            padding: 0;
            list-style: none;
            li {
                flex: 1 0 50%;
                padding: .25rem 2%;
                margin-bottom: 0;
                border-top: 1px solid #c4b581;
                &:nth-child(even) {
                    padding-right: 0;
                }
                &:nth-child(odd) {
                    padding-left: 0;
                    border-right: 1px solid #c4b581;
                }
                &:first-child,
                &:nth-child(2) {
                    border-top: 0;
                }
            }
        }
    }
}


/* Only used on Resources t5 in OTR */


/* Global Give */

.global-give.button-top {
    position: fixed;
    bottom: 70px;
    left: 20px;
    z-index: 200;
    width: 90%;
    a {
        background: #00573d;
        padding: 1rem;
        width: 100%;
        display: block;
        border-radius: 4px;
        text-align: center;
        border-radius: 3.25em;
        border-bottom: 1px solid #fff;
        box-shadow: rgba(0, 0, 0, 0.5) 0 4px 0;
        transition: background 1s ease-in-out;
        text-decoration: none;
        &:hover,
        &:focus {
            background: #0FA153;
        }
    }
    @media screen and (min-width: 660px) {
        padding: 0 10vw;
    }
    @media screen and (min-width: 1000px) {
        position: fixed;
        bottom: 20px;
        left: 20px;
        width: 20%;
        padding: unset;
    }
}


